.intl-tel-input {
  position: relative;
  * {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
  }
  .hide {
    display: none;
  }
  .v-hide {
    visibility: hidden;
  }
  input {
    position: relative;
    z-index: 0;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    padding-left: 48px;
    margin-left: 0;
    transition: background-color 100ms ease-out;
    &[type=text], &[type=tel] {
      position: relative;
      z-index: 0;
      margin-top: 0 !important;
      margin-bottom: 0 !important;
      padding-left: 48px;
      margin-left: 0;
      transition: background-color 100ms ease-out;
    }
    &.iti-invalid-key {
      transition: background-color 0;
      background-color: #FFC7C7;
    }
  }
  .flag-container {
    position: absolute;
    top: 0;
    bottom: 0;
    padding: 1px;
    &:hover {
      cursor: pointer;
      .selected-flag {
        background-color: rgba(0, 0, 0, 0.05);
      }
    }
  }
  input {
    &[disabled] + .flag-container:hover, &[readonly] + .flag-container:hover {
      cursor: default;
    }
    &[disabled] + .flag-container:hover .selected-flag, &[readonly] + .flag-container:hover .selected-flag {
      background-color: transparent;
    }
  }
  .selected-flag {
    z-index: 1;
    position: relative;
    width: 42px;
    height: 100%;
    padding: 0 0 0 8px;
    .iti-flag {
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
    }
    .iti-arrow {
      position: absolute;
      top: 50%;
      margin-top: -2px;
      right: 4px;
      width: 0;
      height: 0;
      border-left: 3px solid transparent;
      border-right: 3px solid transparent;
      border-top: 4px solid #555;
      &.up {
        border-top: none;
        border-bottom: 4px solid #555;
      }
    }
  }
  .country-list {
    list-style: none;
    position: absolute;
    z-index: 5;
    padding: 0;
    margin: 0 0 0 -1px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
    background-color: white;
    border: 1px solid #CCC;
    white-space: nowrap;
    max-height: 200px;
    overflow-y: scroll;
    &.dropup {
      bottom: 100%;
      margin-bottom: -1px;
    }
    .flag-box {
      display: inline-block;
      width: 20px;
    }
    .divider {
      padding-bottom: 5px;
      margin-bottom: 5px;
      border-bottom: 1px solid #CCC;
    }
    .country {
      padding: 5px 10px;
      .dial-code {
        color: #999;
      }
      &.highlight {
        background-color: rgba(0, 0, 0, 0.05);
      }
    }
    .flag-box, .country-name, .dial-code {
      vertical-align: middle;
    }
    .flag-box, .country-name {
      margin-right: 6px;
    }
  }
  select {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 42px;
    height: 100%;
    opacity: 0;
  }
  &.iti-container {
    position: absolute;
    top: -1000px;
    left: -1000px;
    z-index: 1060;
    padding: 1px;
    &:hover {
      cursor: pointer;
    }
  }
}

@media (max-width: 500px) {
  .intl-tel-input .country-list {
    white-space: normal;
  }
}

